@import url(https://fonts.googleapis.com/css2?family=Chivo:wght@200;400;700&display=swap);
*{font-family:"Chivo",sans-serif;box-sizing:border-box;text-rendering:optimizeSpeed}body{margin:0;background-color:#fff}.scene-tooltip{display:none}.head{background-color:#16181a;color:#fff;display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding:0 1rem;height:3rem;overflow:hidden;font-size:1rem}.head .left{display:flex;flex-direction:row;align-items:center;justify-content:center;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem}.head .left .toggle{height:24px;cursor:pointer;transition:0.3s all}.head .left .toggle:hover{opacity:0.5;transition:0.3s all}.head .left .logo{display:inline-block;font-weight:700;cursor:pointer;transition:0.3s all;text-decoration:none;color:#fff}.head .left .logo span{opacity:0.5;font-weight:400}.head .left .logo:hover{color:#969da5;transition:0.3s all}.head .right{display:flex;flex-direction:row;align-items:center;justify-content:right;grid-column-gap:1.25rem;-webkit-column-gap:1.25rem;column-gap:1.25rem}.head .right a{color:#fff;text-decoration:none;transition:0.3s all}.head .right a:hover{color:#969da5;transition:0.3s all}.pane{height:100%}.pane .index{position:absolute;width:20rem;height:calc(100vh - 3rem);background-color:#16181a;display:flex;flex-direction:column;padding:0rem 1rem;z-index:100;transition:0.3s all}.pane .index input[type="text"]{font-size:1rem;display:block;height:2.5rem;border:none;border-radius:0;background:transparent;color:#fff;outline:none;border-bottom:2px solid #969da5;width:100%}.pane .index input[type="text"]:focus{border-bottom:2px solid #fff;transition:0.3s all}.pane .index ::-webkit-input-placeholder{color:#969da5;opacity:1}.pane .index :-ms-input-placeholder{color:#969da5;opacity:1}.pane .index ::placeholder{color:#969da5;opacity:1}.pane .index button{color:#fff;text-decoration:none;line-height:2.5rem;vertical-align:middle;background:none;border:none;font-size:1rem;padding:0;border-bottom:1px solid #5c646c;display:flex;align-items:center;justify-content:space-between;cursor:pointer;transition:0.3s all}.pane .index button span{vertical-align:middle;border:1px solid #969da5;line-height:1rem;padding:0.2rem;text-transform:uppercase;display:inline-block;opacity:0.5}.pane .index button:hover{opacity:0.5;transition:0.3s all}.pane .details{position:absolute;width:25rem;height:calc(100vh - 3rem);background-color:rgba(255,255,255,0.75);padding:2rem;transition:0.3s all;display:flex;flex-direction:column;scroll-behavior:smooth;overflow-y:scroll;border-right:2px solid #dcdcdd}.pane .details h1{font-size:1.4rem;margin-top:0;color:#16181a}.pane .details h1 span{color:#969da5;margin-left:0.5rem;text-transform:uppercase;font-weight:200;font-size:1rem;border:1px solid #969da5;padding:0.15rem 0.2rem}.pane .details p{line-height:1.4;color:#5c646c}.pane .details ul{list-style:none;margin:0;padding:0}.pane .details ul li{display:block;padding:0.5rem 0}.pane .details ul li a{color:#089ec5;text-decoration:none}.pane .details ul li a:hover{opacity:0.5}.pane .details ul li:not(:last-child){border-bottom:1px solid #dcdcdd}.pane .details .spacer{height:2rem}.pane .details .actions{display:flex;flex-direction:row;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;justify-items:space-between}.pane .details a{transition:0.2s all}.pane .details a.download{display:inline-block;background-color:#089ec5;text-decoration:none;color:#fff;padding:0.5rem;text-align:center;margin:0.5rem 0;width:100%}.pane .details a.edit{display:inline-block;background-color:#dcdcdd;text-decoration:none;color:#5c646c;padding:0.5rem;text-align:center;margin:0.5rem 0;width:100%}.pane .details a:hover{opacity:0.5;transition:0.2s all}.pane .details h6{color:#969da5;font-size:0.8rem;text-transform:uppercase;font-weight:200;margin-top:2rem;margin-bottom:0.5rem;letter-spacing:1px}body .graph{z-index:-100;position:absolute;top:0px;left:0px;width:100vw;min-height:100vh;background-color:#fff}.node-label{font-size:1rem;padding:0.2em 0.5rem;max-width:10rem;text-align:center;overflow-wrap:normal;color:#16181a}.node-label.default{color:#1985A1;border:1px solid #1985A1;background:#fff}.node-label.active{font-size:1rem;color:#ffffff;background:#1985A1;z-index:1000}.logo img{position:absolute;bottom:50px;right:50px;width:150px;height:auto;opacity:0.25;transition:0.3s all}.logo img:hover{opacity:1;transition:0.3s all}canvas{width:100%;height:100%}.opacity-50{opacity:0.5}

